<template>
  <div class="content-preOrder">
    <div class="content-header">
      <search @emitSearchFun="searchFun($event)" @emitClearFun="clearFun()" tips="姓名/手机号码"/>
    </div>
    <div class="content-main">
      <div class="list" v-if="list.length>0">
        <div class="item" v-for="(item, index) in list" :key="index" @click="gotoFun(item)">
          <div class="num">{{index}}</div>
          <div class="restaurant-code">{{item.tableno}}</div>
          <div class="class-name">{{item.shift}}</div>
          <div class="order-person">{{item.resName}}</div>
          <div class="order-phoneNumber">{{item.phone}}</div>
          <div class="iconfont icon-jiantou1"></div>
        </div>
      </div>
      <div class="no-list" v-else>
        <noResult></noResult>
      </div>
    </div>
    <mainFooter activeItem="1"></mainFooter>
  </div>
</template>

<script>
import ajaxGet from "@/conf/ajaxGet";
import { Toast } from "mint-ui";
import search from "@/components/common/mobileOrdering/search.vue";
import mainFooter from "@/components/common/mobileOrdering/mainFooter.vue";
import noResult from "@/components/common/mobileOrdering/noResult.vue";
export default {
  components: {
    search,
    mainFooter,
    noResult
  },
  data() {
    return {
      list: [],
      tempList: []
    };
  },
  mounted() {
    this.$store.commit("clearEntryPreOrderUrl");
    this.$store.commit("clearSelectedPreOrder");
    this.findRess("empty");
  },

  methods: {
    searchFun(val) {
      //console.log(val);
      this.list = this.tempList;
      let filteAll = [];
      let listarr = [];
      this.list.forEach(ele => {
        if (ele.resName.toUpperCase().indexOf(val) > -1) {
          filteAll.push(ele);
        }
        if (ele.phone.indexOf(val) > -1) {
          filteAll.push(ele);
        }
      });

      for (let i = filteAll.length - 1; i >= 0; i--) {
        let pd = false;
        listarr.forEach(opt => {
          if (
            opt.resName == filteAll[i].resName &&
            opt.phone == filteAll[i].phone
          ) {
            pd = true;
          }
        });
        if (!pd) {
          listarr.push(filteAll[i]);
        }
      }
      this.list = listarr;
    },
    clearFun() {
      this.findRess();
    },
    gotoFun(item) {
      //console.log("preOrderitem:", item);
      this.$store.commit("submitSelectedPreOrder", item);
      this.$store.commit("submiEntryPreOrderUrl", "/mobileOrdering/preOrder");
      this.$router.push("/mobileOrdering/preOrderDetails");
    },
    async findRess() {
      let data = {
        params: {}
      };
      let res = await ajaxGet("/pos/router?method=findRess", data);
      if (res && res.resultCode == 0) {
        this.list = res.data;
        this.tempList = res.data;
      }
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "middle",
        duration: 3000
      });
    }
  }
};
</script>
<style lang="less" scoped>
.content-preOrder {
  .content-header {
    background: #393c41;
    padding: 14px 18px 7px;
  }
  .content-main {
    .list {
      position: absolute;
      top: 46px;
      bottom: 50px;
      width: 100%;
      padding-left: 15px;
      overflow-y: auto;
      box-sizing: border-box;
      .item {
        padding-right: 15px;
        border-bottom: 1px solid #ddd;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        font-size: 14px;
        padding: 15px 0;
        .num {
          width: 25px;
        }
        .restaurant-code {
          width: 50px;
          margin-right: 5px;
          color: #dd0a09;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
        .class-name {
          width: 35px;
        }
        .order-person {
          flex: 1;
        }
        .order-phoneNumber {
          width: 100px;
        }
        .iconfont {
          width: 20px;
          margin-right: 10px;
        }
      }
    }

    .no-list {
      margin-top: 150px;
    }
  }
}
</style>



